type ProjectileType = { ctx: CanvasRenderingContext2D; x: number; y: number; r: number; color: string; velocity: { x: number; y: number; speed: number } };

class Projectile {
    ctx: CanvasRenderingContext2D;
    x: number;
    y: number;
    r: number;
    color: string;
    velocity: { x: number; y: number; speed: number };

    constructor({ ctx, x, y, r, color, velocity }: ProjectileType) {
        this.ctx = ctx;
        this.x = x;
        this.y = y;
        this.r = r;
        this.color = color;
        this.velocity = velocity;
    }

    draw() {
        this.ctx.beginPath();
        this.ctx.arc(this.x, this.y, this.r, 0, 2 * Math.PI);
        this.ctx.fillStyle = this.color;
        this.ctx.fill();
        this.ctx.closePath();
    }

    update() {
        this.x += this.velocity.speed * this.velocity.x;
        this.y += this.velocity.speed * this.velocity.y;
        this.draw();
    }
}

export default Projectile;
