import { ReactNode, useState } from 'react';
import styled from 'styled-components';

type FlipProps = { front: ReactNode; back: ReactNode; width?: string; height?: string };

const Outter = styled.div`
    cursor: pointer;
`;
const Inner = styled.div`
    position: relative;
    width: 100%;
    height: 100%;
    transition: transform 1s;
    transform-style: preserve-3d;

    &.flip {
        transform: rotateY(180deg);
    }
`;
const Front = styled.div`
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
`;
const Back = styled(Front)`
    transform: rotateY(180deg);
`;

export default function Flip({ front, back, width = '100%', height = '100%' }: FlipProps) {
    const [filp, setFlip] = useState(false);
    const onClickHandler = () => setFlip(state => !state);
    return (
        <Outter style={{ width, height }}>
            <Inner className={filp ? 'flip' : ''} onClick={onClickHandler}>
                <Front>{front}</Front>
                <Back>{back}</Back>
            </Inner>
        </Outter>
    );
}
