import { ReactNode, useState, useRef, useEffect } from 'react';

import styled from 'styled-components';

const Outter = styled.div`
    cursor: pointer;
    transition: height 1s;
`;

const Summary = styled.div``;
const Panel = styled.div`
    overflow: hidden;
    transition: 0.5s;
    &.active {
        height: ${({ contentHeight }: { contentHeight: number }) => `${contentHeight}px`};
    }
    &.hidden {
        height: 0;
    }
`;

export default function Accordion({ summary, children }: { summary: ReactNode; children: ReactNode }) {
    const [contentHeight, setContentHeight] = useState(0);
    const [className, setClassName] = useState('');
    const panel = useRef<HTMLDivElement>(null);
    const onClickHandler = () => {
        const target = panel.current;
        if (target) {
            target.classList.toggle('active');
            target.classList.toggle('hidden');
        }
    };

    useEffect(() => {
        const target = panel.current;
        if (target) {
            setContentHeight(target.offsetHeight);
            setClassName('hidden');
        }
    }, []);

    return (
        <Outter>
            <Summary onClick={onClickHandler}>{summary}</Summary>
            <Panel ref={panel} className={className} contentHeight={contentHeight}>
                {children}
            </Panel>
        </Outter>
    );
}
