import { useEffect } from 'react';
import { getRandomColor } from '../../utils/helper';
import Enemy from './libs/enemy';

import Player from './libs/player';
import Projectile from './libs/projectile';
import Particle from './libs/particle';

const useTestCanvas = () => {
    useEffect(() => {
        const canvas = document.getElementById('test') as HTMLCanvasElement;
        const ctx = canvas.getContext('2d');

        const projectileCount = 5;

        const projectiles: Projectile[] = [];
        const enemies: Enemy[] = [];
        const particles: Particle[] = [];

        const gap = { x: 10, y: 5 };
        const offset = { x: [-gap.x * 2, -gap.x, 0, gap.x, gap.x * 2], y: [gap.y * 2, gap.y, 0, gap.y, gap.y * 2] };
        let animationFrame: number;

        if (canvas && ctx) {
            const player = new Player({ ctx, x: canvas.width / 2, y: canvas.height / 2, r: 10, color: 'red' });

            setInterval(() => {
                const enemy = new Enemy({
                    ctx,
                    x: Math.random() * canvas.width,
                    y: 0,
                    r: Math.random() * 5 + 5,
                    color: getRandomColor(),
                    target: { x: player.x, y: player.y }
                });
                enemies.push(enemy);
            }, 100);

            setInterval(() => {
                for (let index = 0; index < projectileCount; index++) {
                    const projectile = new Projectile({
                        ctx,
                        x: player.x + offset.x[index],
                        y: player.y + offset.y[index] - 10,
                        r: 5,
                        color: 'blue',
                        velocity: { x: Math.random() * 2 - 1, y: -1, speed: 5 }
                    });
                    projectiles.push(projectile);
                }
            }, 150);

            player.draw();

            const animate = () => {
                animationFrame = requestAnimationFrame(animate);
                ctx.fillStyle = 'rgba(255, 255, 255, 0.2)';
                ctx.fillRect(0, 0, canvas.width, canvas.height);
                player.draw();

                projectiles.forEach((el, i) => {
                    if (el.y < 0 - 1000) setTimeout(() => projectiles.splice(i, 1));
                    else el.update();
                });

                particles.forEach((el, i) => {
                    if (el.alpha < 0.1) setTimeout(() => particles.splice(i, 1));
                    else el.update();
                });

                enemies.forEach((el, i) => {
                    el.update();
                    projectiles.forEach((el2, j) => {
                        const dist = Math.hypot(el.x - el2.x, el.y - el2.y);
                        if (dist - el.r - el2.r < 1) {
                            for (let index = 0; index < 2 * Math.ceil(el.r); index++) {
                                particles.push(new Particle({ ctx, x: el.x, y: el.y, r: Math.random() * 2 + 1, color: el.color }));
                                setTimeout(() => particles.splice(0, 1));
                            }
                            setTimeout(() => {
                                enemies.splice(i, 1);
                                projectiles.splice(j, 1);
                            });
                        }
                    });
                    const dist = Math.hypot(el.x - player.x, el.y - player.y);
                    if (dist - el.r - player.r < 1) cancelAnimationFrame(animationFrame);
                    if (el.y > canvas.height + 1000) setTimeout(() => enemies.splice(i, 1));
                });
            };

            canvas.addEventListener('mousemove', e => {
                const x = e.clientX - canvas.offsetLeft;
                const y = e.clientY - canvas.offsetTop;
                player.setPoint(x, y);
            });

            animate();
        }
    }, []);
};

export default useTestCanvas;
