type PlayerType = { ctx: CanvasRenderingContext2D; x: number; y: number; r: number; color: string };

class Player {
    ctx: CanvasRenderingContext2D;
    x: number;
    y: number;
    r: number;
    color: string;
    constructor({ ctx, x, y, r, color }: PlayerType) {
        this.ctx = ctx;
        this.x = x;
        this.y = y;
        this.r = r;
        this.color = color;
    }
    draw() {
        this.ctx.beginPath();
        this.ctx.arc(this.x, this.y, this.r, 0, 2 * Math.PI);
        this.ctx.fillStyle = this.color;
        this.ctx.fill();
        this.ctx.closePath();
    }

    setPoint(x: number, y: number) {
        this.x = x;
        this.y = y;
    }    
}

export default Player;
