import { Bar, BarChart, CartesianGrid, Cell, ResponsiveContainer, Tooltip, XAxis, YAxis } from 'recharts';
import styled from 'styled-components';
import Grid from '../../../components/Grid';

const colors = ['#ff0000', '#0000ff', '#00C49F'];

const ChartOutter = styled.div`
    height: 300px;
    padding: 1rem 2rem;
`;

export default function HAL({ data }: { data: any }) {
    const { accHigh, accLow, accDrawClose } = data;
    const barData = [
        { name: 'accHigh', value: accHigh },
        { name: 'accLow', value: Math.abs(accLow) },
        { name: 'accDrawClose', value: Math.abs(accDrawClose) }
    ];    
    
    return (
        <div>
            <div className='text-center bold mb-1'>HAL</div>
            <div className='text-center bold mb-1'>total : {(accHigh + accLow + accDrawClose).toFixed(2)}%</div>
            <Grid columns='3'>
                <div className='text-center bold'>
                    <div>H</div>
                    <div>{accHigh}%</div>
                </div>
                <div className='text-center bold'>
                    <div>L</div>
                    <div>{accLow}%</div>
                </div>
                <div className='text-center bold'>
                    <div>D</div>
                    <div>{accDrawClose}%</div>
                </div>
            </Grid>
            <ChartOutter>
                <ResponsiveContainer width='100%' height='100%'>
                    <BarChart data={barData}>
                        <CartesianGrid strokeDasharray='3 3' />
                        <XAxis dataKey='name' />
                        <YAxis />
                        <Tooltip />
                        <Bar dataKey='value' fill='#8884d8' maxBarSize={50} isAnimationActive={false}>
                            {barData.map((entry, index) => (
                                <Cell key={`cell-${index}`} fill={colors[index % 20]} />
                            ))}
                        </Bar>
                    </BarChart>
                </ResponsiveContainer>
            </ChartOutter>
        </div>
    );
}
