type EnemyType = { ctx: CanvasRenderingContext2D; x: number; y: number; r: number; color: string; target: { x: number; y: number } };

class Enemy {
    ctx: CanvasRenderingContext2D;
    x: number;
    y: number;
    r: number;
    color: string;
    dx: number;
    dy: number;

    constructor({ ctx, x, y, r, color, target }: EnemyType) {
        const angle = Math.atan2(y - target.y, x - target.x);
        const speed = 5;

        this.ctx = ctx;
        this.x = x;
        this.y = y;
        this.r = r;
        this.color = color;
        this.dx = Math.cos(angle) * speed;
        this.dy = Math.sin(angle) * speed;
    }

    draw() {
        this.ctx.beginPath();
        this.ctx.arc(this.x, this.y, this.r, 0, 2 * Math.PI);
        this.ctx.fillStyle = this.color;
        this.ctx.fill();
        this.ctx.closePath();
    }

    update() {
        this.x -= this.dx;
        this.y -= this.dy;
        this.draw();
    }
}

export default Enemy;
