import { Cell, Pie, PieChart, ResponsiveContainer } from 'recharts';
import styled from 'styled-components';
import Grid from '../../../components/Grid';

type PropsType = { win: number; lose: number; draw: number; accHigh: number; accLow: number; accDrawHigh: number; accDrawLow: number };
const colors = ['#0088FE', '#00C49F', '#FFBB28'];
const RADIAN = Math.PI / 180;

const CustomizedLabel = (props: any) => {
    const { cx, cy, name, midAngle, outerRadius, percent } = props;
    const radius = outerRadius * 0.65;
    const x = cx + radius * Math.cos(midAngle * RADIAN);
    const y = cy + radius * Math.sin(-midAngle * RADIAN);

    return (
        <g>
            <text x={x} y={y} fill='white' textAnchor='middle'>{`${(percent * 100).toFixed(2)}%`}</text>
            <text x={x} y={y + 20} fill='white' textAnchor='middle'>
                {name}
            </text>
        </g>
    );
};

const ChartOutter = styled.div`
    height: 300px;
`;

export default function WinningPercentage({ data }: { data: PropsType }) {
    const pieData = [
        { name: 'win', value: data.win },
        { name: 'lose', value: data.lose },
        { name: 'draw', value: data.draw }
    ];
    const total = pieData.reduce((p, c) => (p += c.value), 0);

    return (
        <div>
            <div className='text-center bold mb-1'> total : {total}</div>
            <Grid columns='3'>
                {pieData.map(el => (
                    <div key={el.name} className='text-center bold'>
                        {el.name} : {el.value}
                    </div>
                ))}
            </Grid>
            <ChartOutter>
                <ResponsiveContainer width='100%' height='100%'>
                    <PieChart>
                        <Pie data={pieData} cx='50%' cy='50%' dataKey='value' labelLine={false} label={<CustomizedLabel />} isAnimationActive={false}>
                            {pieData.map((el, i) => (
                                <Cell key={`cell-${i}`} fill={colors[i]} />
                            ))}
                        </Pie>
                    </PieChart>
                </ResponsiveContainer>
            </ChartOutter>
        </div>
    );
}
