import { CartesianGrid, ComposedChart, Line, ResponsiveContainer, Tooltip, TooltipProps, XAxis, YAxis } from 'recharts';
import styled from 'styled-components';

const colors = ['#ff0000', '#0000ff', '#00C49F'];

const CustomTooltip = ({ active, payload }: TooltipProps<number | string | Array<number | string>, number | string>) => {
    if (active && payload && payload.length) {
        return (
            <div style={{ background: 'rgba(255, 255, 255, 0.7)', padding: '0.5rem' }}>
                <p>{`${payload[0].payload.date} : ${payload[0].payload.name}`}</p>
                <p style={{ color: payload[5].value && payload[5].value >= 0 ? colors[0] : colors[1] }}>{`${payload[5].dataKey} : ${payload[5].value}`}</p>
                <p style={{ color: payload[1].value && payload[1].value >= 0 ? colors[0] : colors[1] }}>{`${payload[1].dataKey} : ${payload[1].value}`}</p>
                <p style={{ color: payload[2].value && payload[2].value >= 0 ? colors[0] : colors[1] }}>{`${payload[2].dataKey} : ${payload[2].value}`}</p>
                <p style={{ color: payload[6].value && payload[6].value >= 0 ? colors[0] : colors[1] }}>{`${payload[6].dataKey} : ${payload[6].value}`}</p>
            </div>
        );
    }
    return null;
};

const CustomDot = (props: any) => {
    const { dataKey, cx, cy, payload } = props;
    const { high, cutUp, low } = payload;

    if (dataKey === 'high' && high >= cutUp) return <circle cx={cx} cy={cy} r={2.5} fill={colors[0]} />;
    if (dataKey === 'low' && high < cutUp && low < -cutUp) return <circle cx={cx} cy={cy} r={2.5} fill={colors[1]} />;
    if (dataKey === 'preClose' && high < cutUp && low >= -cutUp) return <circle cx={cx} cy={cy} r={2.5} />;

    return null;
};

const Outter = styled.div`
    overflow: scroll;
    height: 500px;
    position: relative;
`;

export default function StockLineChart({ data }: { data: any[] }) {
    return (
        <Outter>
            <ResponsiveContainer width={`${data.length < 100 ? '100' : data.length * 1.5}%`} height='100%'>
                <ComposedChart data={data} margin={{ top: 10, right: 20, left: 0, bottom: 10 }}>
                    <XAxis dataKey='name' />
                    <YAxis interval='preserveStartEnd' />
                    <CartesianGrid strokeDasharray='3 3' />
                    <Tooltip content={<CustomTooltip />} />
                    <Line
                        type='monotone'
                        dataKey='preClose'
                        stroke='black'
                        strokeDasharray='1'
                        dot={<CustomDot />}
                        activeDot={false}
                        isAnimationActive={false}
                    />
                    <Line type='monotone' dataKey='high' stroke='red' opacity='0.5' dot={<CustomDot />} activeDot={false} isAnimationActive={false} />
                    <Line type='monotone' dataKey='low' stroke='blue' opacity='0.5' dot={<CustomDot />} activeDot={false} isAnimationActive={false} />
                    <Line type='monotone' dataKey='cutUp' stroke='gold' strokeWidth='2' dot={false} activeDot={false} isAnimationActive={false} />
                    <Line type='monotone' dataKey='cutDown' stroke='gold' strokeWidth='2' dot={false} activeDot={false} isAnimationActive={false} />
                    <Line type='monotone' dataKey='open' stroke='transprent' opacity='0.5' dot={<CustomDot />} activeDot={false} isAnimationActive={false} />
                    <Line type='monotone' dataKey='close' stroke='transprent' opacity='0.5' dot={<CustomDot />} activeDot={false} isAnimationActive={false} />
                </ComposedChart>
            </ResponsiveContainer>
        </Outter>
    );
}
