import { useRef, useEffect } from 'react';

const useBreakOut = () => {
    const breakOutRef = useRef<HTMLCanvasElement>(null);

    useEffect(() => {
        const canvas = breakOutRef.current;
        const ctx = canvas?.getContext('2d');
        if (canvas && ctx) {
            let x = canvas.width / 2;
            let y = canvas.height - 30;
            let dx = 2;
            let dy = -2;

            const ballRadius = 10;

            const paddleHeight = 10;
            const paddleWidth = canvas.width * 2;
            let paddleX = (canvas.width - paddleWidth) / 2;

            let rightPressed = false;
            let leftPressed = false;

            const brickRowCount = 3;
            const brickColumnCount = 5;
            const brickWidth = 75;
            const brickHeight = 20;
            const brickPadding = 10;
            const brickOffsetTop = 30;
            const brickOffsetLeft = 30;

            let score = 0;

            let animate = false;

            var bricks: { x: number; y: number; status: number }[][] = [];
            for (var c = 0; c < brickColumnCount; c++) {
                bricks[c] = [];
                for (var r = 0; r < brickRowCount; r++) {
                    bricks[c][r] = { x: 0, y: 0, status: 1 };
                }
            }

            const collisionDetection = () => {
                for (var c = 0; c < brickColumnCount; c++) {
                    for (var r = 0; r < brickRowCount; r++) {
                        var b = bricks[c][r];
                        if (b.status === 1) {
                            if (x > b.x && x < b.x + brickWidth && y > b.y && y < b.y + brickHeight) {
                                dy = -dy;
                                b.status = 0;
                                score++;
                            }
                        }
                    }
                }
            };

            const drawBall = () => {
                ctx.beginPath();
                ctx.arc(x, y, ballRadius, 0, Math.PI * 2);
                ctx.fillStyle = '#0095DD';
                ctx.fill();
                ctx.closePath();
            };

            const drawPaddle = () => {
                ctx.beginPath();
                ctx.rect(paddleX, canvas.height - paddleHeight, paddleWidth, paddleHeight);
                ctx.fillStyle = '#0095DD';
                ctx.fill();
                ctx.closePath();
            };

            const drawBricks = () => {
                for (let c = 0; c < brickColumnCount; c++) {
                    for (var r = 0; r < brickRowCount; r++) {
                        if (bricks[c][r].status === 1) {
                            let brickX = c * (brickWidth + brickPadding) + brickOffsetLeft;
                            let brickY = r * (brickHeight + brickPadding) + brickOffsetTop;
                            bricks[c][r].x = brickX;
                            bricks[c][r].y = brickY;
                            ctx.beginPath();
                            ctx.rect(brickX, brickY, brickWidth, brickHeight);
                            ctx.fillStyle = '#0095DD';
                            ctx.fill();
                            ctx.closePath();
                        }
                    }
                }
            };

            const drawScore = () => {
                ctx.font = '16px Arial';
                ctx.fillStyle = '#0095DD';
                ctx.fillText('Score: ' + score, 8, 20);
            };

            const draw = () => {
                if (animate) requestAnimationFrame(draw);
                ctx.clearRect(0, 0, canvas.width, canvas.height);
                drawBricks();
                drawBall();
                drawPaddle();
                drawScore();
                collisionDetection();

                x += dx;
                y += dy;

                if (x + dx > canvas.width - ballRadius || x + dx < ballRadius) {
                    dx = -dx;
                }
                if (y + dy < ballRadius) {
                    dy = -dy;
                } else if (y + dy > canvas.height - ballRadius) {
                    if (x > paddleX && x < paddleX + paddleWidth) {
                        dy = -dy;
                    } else {
                        return alert('GAME OVER');
                    }
                }
                if (rightPressed && paddleX < canvas.width - paddleWidth) paddleX += 7;
                if (leftPressed && paddleX > 0) paddleX -= 7;

                if (score === brickRowCount * brickColumnCount) {
                    return;
                } else {
                    // requestAnimationFrame(draw);
                }
            };

            const keyDownHandler = (e: KeyboardEvent) => {
                if (e.key === 'ArrowRight') {
                    rightPressed = true;
                } else if (e.key === 'ArrowLeft') {
                    leftPressed = true;
                }
            };

            const keyUpHandler = (e: KeyboardEvent) => {
                if (e.key === 'ArrowRight') {
                    rightPressed = false;
                } else if (e.key === 'ArrowLeft') {
                    leftPressed = false;
                }
            };

            const mouseMoveHandler = (e: MouseEvent) => {
                var relativeX = e.clientX - canvas.offsetLeft;

                if (relativeX > 0 && relativeX < canvas.width) {
                    paddleX = relativeX - paddleWidth / 2;
                }
            };

            draw();

            document.addEventListener('keydown', keyDownHandler, false);
            document.addEventListener('keyup', keyUpHandler, false);
            document.addEventListener('mousemove', mouseMoveHandler, false);

            canvas.addEventListener('mouseover', () => {
                animate = true;
                draw();
            });
            canvas.addEventListener('mouseleave', () => {
                animate = false;
            });
        }
    }, []);

    return { breakOutRef };
};

export default useBreakOut;
