import { useState } from 'react';

import Container from './Container';
import Check from './Check';
import Table from './Table';
import StockLineChart from './StockLineChart';
import Grid from '../../components/Grid';
import WinningPercentage from './WinningPercentage';
import HAL from './HAL';
import CUT from './CUT';
import CUTUP from './CUTUP';
import useStockList from './useStockList';
import Filter from './Filter';

export default function Stock() {
    const [confirm, setConfirm] = useState(false);
    const [filter, setFilter] = useState<{ filterKey: 'date' | 'period'; value: { start: string; end: string } }>({
        filterKey: 'period',
        value: { start: '2020. 09. 01', end: '9999. 12. 31' }
    });
    const { filterList, result, date } = useStockList(filter);

    const changeHandler = (e: React.ChangeEvent<HTMLInputElement>) => e.target.value === '9398' && setConfirm(true);
    if (!confirm) return <Check onChange={changeHandler} />;

    return (
        <Container>
            <hr />
            <h3 className='text-center'>TITLE</h3>
            <hr />
            <Filter date={date} filter={filter} setFilter={setFilter} />
            <hr />
            <StockLineChart data={filterList} />
            <hr />
            <h3 className='text-center'>RESULT</h3>
            <hr />
            <h4 className='text-center'>RATE</h4>
            <WinningPercentage data={result} />
            <hr style={{ borderStyle: 'ridge' }} />
            <h4 className='text-center'>RESULT BY CASE</h4>
            <ul>
                <li>
                    open {'>'} cutHigh or open {'<'} cutLow : open S
                </li>
                <li>
                    high {'>'} cutHight : cut S or low {'<'} cutLow : cut S
                </li>
                <li>
                    high {'<'} cutHight and low {'>'} cutLow : close S
                </li>
            </ul>
            <Grid columns='2'>
                <HAL data={result} />
                <CUT data={result} />
                <CUTUP data={result} />
            </Grid>
            <hr style={{ borderStyle: 'ridge' }} />
        </Container>
    );
}
