import React, { ReactNode, useCallback, useRef, useState } from 'react';
import styled from 'styled-components';

const Outter = styled.div`
    position: relative;
    overflow: hidden;
`;

const Inner = styled.div`
    position: relative;
    display: flex;
    transition: 0.5s;
    left: 0;
`;

const Arrow = styled.div`
    position: absolute;
    color: white;
    font-size: 2rem;
    top: calc(50% - 1.5rem);
    width: 100%;

    & > div {
        box-sizing: border-box;
        padding: 1rem;
        width: 100%;
        display: flex;
        justify-content: space-between;
    }
    .prev,
    .next {
        cursor: pointer;
    }
    .none {
        pointer-events: none;
        color: grey;
    }
`;

export default function Carousel2({ children }: { children: ReactNode }) {
    const [index, setIndex] = useState(0);
    const inner = useRef<HTMLDivElement>(null);
    const contents = React.Children.map(children, el => <div>{el}</div>);
    const max = React.Children.count(children);

    const slideHandler = useCallback(
        (n: number) => {
            const target = inner.current;
            const targetIndex = index + n;
            if (target) {
                const contentWidth = target.children.item(0)?.getClientRects()[0].width || 0;
                target.style.left = `${contentWidth * targetIndex}px`;
                setIndex(targetIndex);
            }
        },
        [index]
    );

    return (
        <Outter>
            <Inner ref={inner} style={{ width: `${max * 100}%` }}>
                {contents}
            </Inner>
            <Arrow>
                <div>
                    <div className={`prev ${index === 0 ? 'none' : ''}`} onClick={() => slideHandler(1)}>
                        
                    </div>
                    <div className={`next ${index === -(max - 1) ? 'none' : ''}`} onClick={() => slideHandler(-1)}>
                        
                    </div>
                </div>
            </Arrow>
        </Outter>
    );
}
