type ParticleType = { ctx: CanvasRenderingContext2D; x: number; y: number; r: number; color: string };

class Particle {
    ctx: CanvasRenderingContext2D;
    x: number;
    y: number;
    r: number;
    color: string;
    speed = 5;
    alpha = 1;
    dx = Math.random() - 0.5;
    dy = Math.random() - 0.5;

    constructor({ ctx, x, y, r, color }: ParticleType) {
        this.ctx = ctx;
        this.x = x;
        this.y = y;
        this.r = r;
        this.color = color;
    }

    draw() {
        this.ctx.save();
        this.ctx.globalAlpha = this.alpha;
        this.ctx.beginPath();
        this.ctx.arc(this.x, this.y, this.r, 0, 2 * Math.PI);
        this.ctx.fillStyle = this.color;
        this.ctx.fill();
        this.ctx.closePath();
        this.ctx.restore();
    }

    update() {
        this.x += this.dx * this.speed;
        this.y += this.dy * this.speed;
        this.alpha -= 0.01;
        this.draw();
    }
}

export default Particle;
