import { useMemo } from 'react';
import { stockList } from '../../assets/data/stockList';
import { numToPercent } from '../../utils/helper';

type FilterKeyType = 'date' | 'period';
type ValueType = { start: string; end: string } | string;

const filteredList = stockList.filter(el => el.preClose && el.open && el.high && el.low && el.close);
const list = filteredList.map((el, i) => {
    const { date, name, preClose, high, open, low, close } = el;

    const cut = 10;
    const highPercent = numToPercent((Number(high) - Number(preClose)) / Number(preClose), 2);
    const openPercent = numToPercent((Number(open) - Number(preClose)) / Number(preClose), 2);
    const lowPercent = numToPercent((Number(low) - Number(preClose)) / Number(preClose), 2);
    const closePercent = numToPercent((Number(close) - Number(preClose)) / Number(preClose), 2);
    return { date, name: '***', preClose: 0, high: highPercent, open: openPercent, low: lowPercent, close: closePercent, cutUp: cut, cutDown: -cut };
});

const useStockList = ({ filterKey = 'date', value = 'all' }: { filterKey: FilterKeyType; value: ValueType }) => {
    const filterList = useMemo(() => {
        if (filterKey === 'date') {
            if (value === 'all') return list;
            return list.concat().filter(el => el.date === value);
        }

        if (filterKey === 'period') {
            if (typeof value === 'string') return [];
            return list.concat().filter(el => value.start <= el.date && el.date <= value.end);
        }

        return list;
    }, [filterKey, value]);

    const result = filterList.reduce(
        (p, c) => {
            const { open, high, low, close, cutUp, cutDown } = c;
            if (high >= cutUp) {
                const accCutHigh = Math.max(open, cutUp);
                return { ...p, win: p.win + 1, accHigh: Number((p.accHigh + high).toFixed(2)), accCutHigh: Number((p.accCutHigh + accCutHigh).toFixed(2)) };
            }
            if (low < cutDown) {
                const accCutLow = Math.min(open, cutDown);
                return {
                    ...p,
                    lose: p.lose + 1,
                    accLow: Number((p.accLow + low).toFixed(2)),
                    accCutLow: Number((p.accCutLow + accCutLow).toFixed(2)),
                    accCutClose: Number((p.accCutClose + close).toFixed(2))
                };
            }
            return {
                ...p,
                draw: p.draw + 1,
                accDrawHigh: Number((p.accDrawHigh + high).toFixed(2)),
                accDrawLow: Number((p.accDrawLow + low).toFixed(2)),
                accDrawClose: Number((p.accDrawClose + close).toFixed(2)),
                accCutClose: Number((p.accCutClose + close).toFixed(2))
            };
        },
        { win: 0, lose: 0, draw: 0, accHigh: 0, accLow: 0, accDrawClose: 0, accDrawHigh: 0, accDrawLow: 0, accCutHigh: 0, accCutLow: 0, accCutClose: 0 }
    );

    const date = list.reduce((p, c) => {
        if (!p.includes(c.date)) p.push(c.date);
        return p;
    }, [] as string[]);

    return { filterList, result, date };
};

export default useStockList;
