import { useEffect } from 'react';
import { getRandomColor } from '../../utils/helper';

const useStarField = () => {
    useEffect(() => {
        const canvas = document.getElementById('canvas') as HTMLCanvasElement;
        const c = canvas.getContext('2d');
        const numStars = 1000;
        const radius = 1;
        const focalLength = 50;
        const velocity = 4;
        let centerX: any, centerY: any;
        let stars: any[] = [],
            star;
        let i;
        let animate = false;

        initializeStars();
        function executeFrame() {
            if (animate) requestAnimationFrame(executeFrame);
            moveStars();
            drawStars();
        }
        function initializeStars() {
            centerX = canvas.width / 2;
            centerY = canvas.height / 2;
            stars = [];
            for (i = 0; i < numStars; i++) {
                star = {
                    x: Math.random() * canvas.width,
                    y: Math.random() * canvas.height,
                    z: Math.random() * canvas.width
                };
                stars.push(star);
            }
        }
        function moveStars() {
            for (i = 0; i < numStars; i++) {
                star = stars[i];
                star.z -= velocity;
                if (star.z <= 0) {
                    star.z = canvas.width;
                }
            }
        }
        function drawStars() {
            var pixelX, pixelY, pixelRadius;
            // Resize to the screen

            if (c) {
                c.fillStyle = 'black';
                c.fillRect(0, 0, canvas.width, canvas.height);
                for (i = 0; i < numStars; i++) {
                    c.fillStyle = getRandomColor();
                    star = stars[i];
                    pixelX = (star.x - centerX) * (focalLength / star.z) * 5;
                    pixelX += centerX;
                    pixelY = (star.y - centerY) * (focalLength / star.z) * 5;
                    pixelY += centerY;
                    pixelRadius = radius * (focalLength / star.z) * 2;
                    c.beginPath();
                    c.arc(pixelX, pixelY, pixelRadius, 0, 2 * Math.PI);
                    c.fill();
                }
            }
        }
        executeFrame();

        canvas.addEventListener('mouseover', () => {
            animate = true;
            executeFrame();
        });
        canvas.addEventListener('mouseleave', () => {
            animate = false;
        });
    }, []);
};

export default useStarField;
