import React from 'react';
import styled from 'styled-components';

type FilterType = {
    filterKey: 'date' | 'period';
    value: { start: string; end: string };
};

const Outter = styled.div`
    margin: auto;
    width: fit-content;
    select {
        margin-left: 1rem;
    }
`;

const Select = styled.select`
    font-size: 1rem;
    padding: 0.5rem 1rem;
`;

export default function Filter({
    date,
    filter,
    setFilter
}: {
    date: string[];
    filter: FilterType;
    setFilter: React.Dispatch<React.SetStateAction<FilterType>>;
}) {
    const { filterKey } = filter;
    const onChangeHandler = (e: React.ChangeEvent<HTMLSelectElement>, from: 'start' | 'end') =>
        setFilter({ ...filter, value: { ...filter.value, [from]: e.target.value } });

    return (
        <Outter>
            <span>FILTER</span>
            <Select>
                <option>period</option>
            </Select>
            {filterKey === 'period' && (
                <>
                    <Select onChange={e => onChangeHandler(e, 'start')}>
                        {date.map(el => (
                            <option key={`${el}-start`}>{el}</option>
                        ))}
                    </Select>
                    <Select onChange={e => onChangeHandler(e, 'end')} defaultValue={date[date.length - 1]}>
                        {date
                            .filter(el => el >= filter.value.start)
                            .map((el, i) => (
                                <option key={`${el}-end`}>{el}</option>
                            ))}
                    </Select>
                </>
            )}
        </Outter>
    );
}
