import useBreakOut from './useBreakout';
import useStarField from './useStarField';
import useTestCanvas from './useTestCanvas';

export default function Canvas() {
    const { breakOutRef } = useBreakOut();
    useStarField();
    useTestCanvas();

    return (
        <>
            <div>
                <canvas ref={breakOutRef} width='480' height='320'></canvas>
                <canvas id='canvas' width='480' height='320'></canvas>
                <canvas id='test' width='480' height='320'></canvas>
            </div>
        </>
    );
}
