type GetIncomType = { base: number; open: number; close: number; high: number; low: number; limit: number };

export const numToPercent = (num: number, n: number) => Math.round(num * Math.pow(10, n + 2)) / Math.pow(10, n);
export const getColor = (num: number) => (num > 0 ? 'red' : num === 0 ? 'black' : 'blue');
export const getIncome = ({ base, open, close, high, low, limit }: GetIncomType) => {
    const openRate = numToPercent((open - base) / base, 2);
    const closeRate = numToPercent((close - base) / base, 2);
    const hightRate = numToPercent((high - base) / base, 2);
    const lowRate = numToPercent((low - base) / base, 2);

    //         
    if (Math.abs(openRate) > limit) return openRate;

    //      
    if (hightRate > 0 && hightRate > limit) return limit;
    if (lowRate < 0 && lowRate < -limit) return -limit;

    //         
    if (Math.abs(closeRate) < limit) return closeRate;
};

export const getRandomColor = () => {
    const letters = '0123456789ABCDEF';
    let color = '#';
    for (var i = 0; i < 6; i++) {
        color += letters[Math.floor(Math.random() * 16)];
    }

    return color;
};
