import { Bar, BarChart, CartesianGrid, Cell, ResponsiveContainer, Tooltip, XAxis, YAxis } from 'recharts';
import styled from 'styled-components';
import Grid from '../../../components/Grid';

const colors = ['#ff0000', '#0000ff', '#00C49F'];

const ChartOutter = styled.div`
    height: 300px;
    padding: 1rem 2rem;
`;

export default function CUTUP({ data }: { data: any }) {
    const { accCutHigh, accCutClose } = data;

    const barData = [
        { name: 'accCutHigh', value: accCutHigh },
        { name: 'accCutClose', value: Math.abs(accCutClose) }
    ];

    return (
        <div>
            <div className='text-center bold mb-1'>CUTUP</div>
            <div className='text-center bold mb-1'>total : {(accCutHigh + accCutClose).toFixed(2)}%</div>
            <Grid columns='2'>
                <div className='text-center bold'>
                    <div>CH</div>
                    <div>{accCutHigh}%</div>
                </div>
                <div className='text-center bold'>
                    <div>CC</div>
                    <div>{accCutClose}%</div>
                </div>
            </Grid>
            <ChartOutter>
                <ResponsiveContainer width='100%' height='100%'>
                    <BarChart data={barData}>
                        <CartesianGrid strokeDasharray='3 3' />
                        <XAxis dataKey='name' />
                        <YAxis height={1600} />
                        <Tooltip />
                        <Bar dataKey='value' fill='#8884d8' maxBarSize={50} isAnimationActive={false}>
                            {barData.map((entry, index) => (
                                <Cell key={`cell-${index}`} fill={colors[index % 20]} />
                            ))}
                        </Bar>
                    </BarChart>
                </ResponsiveContainer>
            </ChartOutter>
        </div>
    );
}
