import { ReactNode, useCallback, useEffect, useRef } from 'react';
import styled from 'styled-components';

const Outter = styled.div`
    position: relative;
    overflow: auto;
`;

const Inner = styled.div`
    position: absolute;
    width: 100%;
    transition: 0.5s;
    top: -50px;
`;

const Menu = styled.div`
    display: flex;
    width: 100%;
    height: 100%;
    background: grey;
    padding: 0.5rem;

    & > div {
        color: beige;
        cursor: pointer;
    }

    & > div + div {
        margin-left: 1rem;
    }
`;

const Content = styled.div`
    overflow: scroll;
`;

export default function SlideDownBar({ children }: { children: ReactNode }) {
    const scroll = useRef<HTMLDivElement>(null);
    const bar = useRef<HTMLDivElement>(null);

    const scrollHandler = useCallback(() => {
        const target = bar.current;
        const scrollTarget = scroll.current;

        if (target && scrollTarget) {
            if (scrollTarget.scrollTop > 20) target.style.top = '0';
            else target.style.top = '-50px';
        }
    }, []);
    useEffect(() => {
        const target = scroll.current;
        if (target) {
            target.addEventListener('scroll', scrollHandler);
        }
        return () => {
            if (target) {
                target.removeEventListener('scroll', scrollHandler);
            }
        };
    }, [scrollHandler]);
    return (
        <Outter>
            <Inner ref={bar}>
                <Menu>
                    <div>1</div>
                    <div>2</div>
                    <div>3</div>
                </Menu>
            </Inner>
            <Content ref={scroll}>{children}</Content>
        </Outter>
    );
}
